.predictive-maintenance {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  background: #f8fafc;
  min-height: 100vh;
}
.predictive-maintenance .pm-header {
  margin-bottom: 2rem;
  text-align: center;
}
.predictive-maintenance .pm-header .pm-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 1rem 0;
  line-height: 1.2;
}
.predictive-maintenance .pm-header .pm-description {
  font-size: 1.125rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}
.predictive-maintenance .pm-actions {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
}
.predictive-maintenance .pm-actions .action-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.predictive-maintenance .pm-actions .action-buttons .btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
}
.predictive-maintenance .pm-actions .action-buttons .btn .btn-icon {
  width: 1rem;
  height: 1rem;
  flex-shrink: 0;
}
.predictive-maintenance .pm-actions .action-buttons .btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-primary {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  box-shadow: 0 4px 6px -1px rgba(59, 130, 246, 0.1), 0 2px 4px -1px rgba(59, 130, 246, 0.06);
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-primary:hover:not(:disabled) {
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  box-shadow: 0 10px 15px -3px rgba(59, 130, 246, 0.1), 0 4px 6px -2px rgba(59, 130, 246, 0.05);
  transform: translateY(-1px);
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-primary:active:not(:disabled) {
  transform: translateY(0);
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-secondary {
  background: #f8fafc;
  color: #475569;
  border: 1px solid #e2e8f0;
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-secondary:hover:not(:disabled) {
  background: #f1f5f9;
  border-color: #cbd5e1;
  transform: translateY(-1px);
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-secondary:active:not(:disabled) {
  transform: translateY(0);
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-outline {
  background: transparent;
  color: #3b82f6;
  border: 1px solid #3b82f6;
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-outline:hover:not(:disabled) {
  background: #3b82f6;
  color: white;
  transform: translateY(-1px);
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-outline:active:not(:disabled) {
  transform: translateY(0);
}
.predictive-maintenance .pm-actions .action-buttons .btn.btn-sm {
  padding: 0.5rem 1rem;
  font-size: 0.75rem;
}
.predictive-maintenance .pm-actions .action-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.predictive-maintenance .pm-actions .action-info .info-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #6b7280;
  font-size: 0.875rem;
}
.predictive-maintenance .pm-actions .action-info .info-item .info-icon {
  width: 1rem;
  height: 1rem;
  flex-shrink: 0;
}
.predictive-maintenance .pm-actions .action-info .info-item .info-text {
  font-weight: 500;
}
.predictive-maintenance .pm-summary {
  margin-bottom: 2rem;
}
.predictive-maintenance .pm-summary .summary-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  transition: all 0.2s ease;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.predictive-maintenance .pm-summary .summary-grid .summary-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border-color: #d1d5db;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  border-radius: 0.75rem;
  flex-shrink: 0;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon svg {
  width: 1.5rem;
  height: 1.5rem;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon.assets-icon {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon.risk-icon {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon.savings-icon {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon.confidence-icon {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  color: white;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-content {
  flex: 1;
  min-width: 0;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-content .card-value {
  font-size: 2rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 0.25rem;
  line-height: 1;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-content .card-value.risk-value {
  color: #dc2626;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-content .card-value.savings-value {
  color: #059669;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-content .card-value.confidence-value {
  color: #7c3aed;
}
.predictive-maintenance .pm-summary .summary-grid .summary-card .card-content .card-label {
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
}
.predictive-maintenance .pm-predictions .predictions-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 1.5rem 0;
}
.predictive-maintenance .pm-predictions .predictions-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  transition: all 0.2s ease;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border-color: #d1d5db;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 0.75rem;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-header .asset-name {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
  flex: 1;
  min-width: 0;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-header .risk-badge {
  padding: 0.375rem 0.75rem;
  border-radius: 0.5rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  border: 1px solid transparent;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-header .risk-badge.risk-high {
  background: #fef2f2;
  color: #dc2626;
  border-color: #fecaca;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-header .risk-badge.risk-medium {
  background: #fffbeb;
  color: #d97706;
  border-color: #fed7aa;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-header .risk-badge.risk-low {
  background: #f0fdf4;
  color: #16a34a;
  border-color: #bbf7d0;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-details .detail-item .detail-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  margin-bottom: 0.25rem;
  display: block;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-details .detail-item .detail-value {
  font-size: 0.875rem;
  color: #374151;
  font-weight: 500;
}
.predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #e5e7eb;
}
.predictive-maintenance .empty-state,
.predictive-maintenance .loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.predictive-maintenance .empty-state .empty-icon,
.predictive-maintenance .empty-state .loading-spinner,
.predictive-maintenance .loading-state .empty-icon,
.predictive-maintenance .loading-state .loading-spinner {
  margin-bottom: 1.5rem;
  color: #9ca3af;
}
.predictive-maintenance .empty-state .loading-spinner,
.predictive-maintenance .loading-state .loading-spinner {
  color: #3b82f6;
}
.predictive-maintenance .empty-state .empty-title,
.predictive-maintenance .empty-state .loading-title,
.predictive-maintenance .loading-state .empty-title,
.predictive-maintenance .loading-state .loading-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.75rem 0;
}
.predictive-maintenance .empty-state .empty-description,
.predictive-maintenance .empty-state .loading-description,
.predictive-maintenance .loading-state .empty-description,
.predictive-maintenance .loading-state .loading-description {
  font-size: 1rem;
  color: #6b7280;
  margin: 0 0 2rem 0;
  max-width: 32rem;
  line-height: 1.6;
}
.predictive-maintenance .animate-spin {
  animation: spin 1s linear infinite;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 768px) {
  .predictive-maintenance {
    padding: 1rem;
  }
  .predictive-maintenance .pm-actions {
