    align-items: stretch;
    gap: 1.5rem;
  }
  .predictive-maintenance .pm-actions .action-buttons {
    flex-wrap: wrap;
    justify-content: center;
  }
  .predictive-maintenance .pm-actions .action-buttons .btn {
    flex: 1;
    min-width: 0;
    justify-content: center;
  }
  .predictive-maintenance .pm-actions .action-info {
    justify-content: center;
  }
  .predictive-maintenance .pm-summary .summary-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .predictive-maintenance .pm-summary .summary-grid .summary-card {
    padding: 1.25rem;
  }
  .predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  .predictive-maintenance .pm-summary .summary-grid .summary-card .card-icon svg {
    width: 1.25rem;
    height: 1.25rem;
  }
  .predictive-maintenance .pm-summary .summary-grid .summary-card .card-content .card-value {
    font-size: 1.75rem;
  }
  .predictive-maintenance .pm-predictions .predictions-list .prediction-card {
    padding: 1.25rem;
  }
  .predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-details {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  .predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-actions {
    flex-direction: column;
  }
  .predictive-maintenance .pm-predictions .predictions-list .prediction-card .prediction-actions .btn {
    width: 100%;
  }
  .predictive-maintenance .empty-state,
  .predictive-maintenance .loading-state {
    padding: 3rem 1.5rem;
  }
  .predictive-maintenance .empty-state .empty-title,
  .predictive-maintenance .empty-state .loading-title,
  .predictive-maintenance .loading-state .empty-title,
  .predictive-maintenance .loading-state .loading-title {
    font-size: 1.25rem;
  }
  .predictive-maintenance .empty-state .empty-description,
  .predictive-maintenance .empty-state .loading-description,
  .predictive-maintenance .loading-state .empty-description,
  .predictive-maintenance .loading-state .loading-description {
    font-size: 0.875rem;
  }
}
